const ResultsPage = () => {
  const [currentPage, setCurrentPage] = useState(1);
  const [filterCabor, setFilterCabor] = useState('');
  const [filterNomor, setFilterNomor] = useState('');
  const resultsPerPage = 10;
  
  // Filter data berdasarkan cabang olahraga dan nomor
  const filteredResults = allResults.filter(result => {
    return (filterCabor === '' || result.cabangOlahraga.includes(filterCabor)) &&
           (filterNomor === '' || result.nomor.includes(filterNomor));
  });

  // Pagination
  const indexOfLastResult = currentPage * resultsPerPage;
  const indexOfFirstResult = indexOfLastResult - resultsPerPage;
  const currentResults = filteredResults.slice(indexOfFirstResult, indexOfLastResult);
  const totalPages = Math.ceil(filteredResults.length / resultsPerPage);

  // Dapatkan daftar cabang olahraga unik untuk dropdown filter
  const uniqueCabors = [...new Set(allResults.map(item => item.cabangOlahraga))];

  return (
    <div style={{ 
      minHeight: '100vh', 
      display: 'flex', 
      flexDirection: 'column',
      backgroundColor: 'var(--background)',
      color: 'var(--foreground)'
    }}>
      <Navbar />
      
      <main className="flex-grow container mx-auto px-4 py-8">
        <h1 className="text-center mb-8" style={{ 
          fontSize: 'var(--font-size-xlarge)',
          fontWeight: 'bold',
          color: 'var(--color-primary)'
        }}>
          HASIL PERTANDINGAN
        </h1>
        
        {/* Filter Section */}
        <div className="mb-8 flex flex-col md:flex-row gap-4">
          <div className="flex-1">
            <label className="block mb-2" style={{ color: 'var(--color-gray-700)' }}>Cabang Olahraga</label>
            <select 
              value={filterCabor}
              onChange={(e) => setFilterCabor(e.target.value)}
              className="w-full p-3 rounded-lg border" 
              style={{ 
                borderColor: 'var(--color-gray-300)',
                backgroundColor: 'var(--color-white)'
              }}
            >
              <option value="">Semua Cabang</option>
              {uniqueCabors.map((cabor, index) => (
                <option key={index} value={cabor}>{cabor}</option>
              ))}
            </select>
          </div>
          
          <div className="flex-1">
            <label className="block mb-2" style={{ color: 'var(--color-gray-700)' }}>Nomor Pertandingan</label>
            <input 
              type="text" 
              value={filterNomor}
              onChange={(e) => setFilterNomor(e.target.value)}
              placeholder="Cari nomor pertandingan"
              className="w-full p-3 rounded-lg border" 
              style={{ 
                borderColor: 'var(--color-gray-300)',
                backgroundColor: 'var(--color-white)'
              }}
            />
          </div>
        </div>
        
        {/* Tabel Hasil */}
        <div className="overflow-x-auto rounded-lg shadow-lg mb-8">
          <table className="w-full" style={{ 
            borderCollapse: 'collapse',
            backgroundColor: 'var(--color-white)'
          }}>
            <thead>
              <tr style={{ backgroundColor: 'var(--color-gray-100)' }}>
                <th className="p-4 text-left" style={{ borderBottom: '1px solid var(--color-gray-200)' }}>Cabang Olahraga</th>
                <th className="p-4 text-left" style={{ borderBottom: '1px solid var(--color-gray-200)' }}>Nomor</th>
                <th className="p-4 text-left" style={{ borderBottom: '1px solid var(--color-gray-200)' }}>Kontingen</th>
                <th className="p-4 text-left" style={{ borderBottom: '1px solid var(--color-gray-200)' }}>Babak</th>
                <th className="p-4 text-left" style={{ borderBottom: '1px solid var(--color-gray-200)' }}>Medali</th>
                <th className="p-4 text-left" style={{ borderBottom: '1px solid var(--color-gray-200)' }}>Updates</th>
                <th className="p-4 text-left" style={{ borderBottom: '1px solid var(--color-gray-200)' }}></th>
              </tr>
            </thead>
            <tbody>
              {currentResults.map((result) => (
                <tr key={result.id} style={{ borderBottom: '1px solid var(--color-gray-200)' }}>
                  <td className="p-4">{result.cabangOlahraga}</td>
                  <td className="p-4">{result.nomor}</td>
                  <td className="p-4">{result.kontingen}</td>
                  <td className="p-4">{result.babak}</td>
                  <td className="p-4">
                    <span className={`px-2 py-1 rounded-full ${
                      result.medali === 'EMAS' ? 'bg-yellow-100 text-yellow-800' :
                      result.medali === 'PERAK' ? 'bg-gray-100 text-gray-800' :
                      'bg-yellow-700 text-white'
                    }`}>
                      {result.medali}
                    </span>
                  </td>
                  <td className="p-4">{result.tanggal}</td>
                  <td className="p-4">
                    <Link href={`/hasil-pertandingan/${result.id}`}>
                      <button className="flex items-center justify-center w-10 h-10 rounded-full hover:bg-gray-100 transition-colors">
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="var(--color-primary)">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                        </svg>
                      </button>
                    </Link>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        
        {/* Pagination */}
        <div className="flex justify-center items-center space-x-2 mb-8">
          <button 
            onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
            disabled={currentPage === 1}
            className="px-4 py-2 rounded-md" 
            style={{
              backgroundColor: currentPage === 1 ? 'var(--color-gray-200)' : 'var(--color-primary)',
              color: currentPage === 1 ? 'var(--color-gray-600)' : 'white',
              cursor: currentPage === 1 ? 'not-allowed' : 'pointer'
            }}
          >
            Previous
          </button>
          
          {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
            <button
              key={page}
              onClick={() => setCurrentPage(page)}
              className={`px-4 py-2 rounded-md ${currentPage === page ? 'font-bold' : ''}`}
              style={{
                backgroundColor: currentPage === page ? 'var(--color-primary)' : 'var(--color-gray-100)',
                color: currentPage === page ? 'white' : 'var(--color-gray-800)'
              }}
            >
              {page}
            </button>
          ))}
          
          <button 
            onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
            disabled={currentPage === totalPages}
            className="px-4 py-2 rounded-md" 
            style={{
              backgroundColor: currentPage === totalPages ? 'var(--color-gray-200)' : 'var(--color-primary)',
              color: currentPage === totalPages ? 'var(--color-gray-600)' : 'white',
              cursor: currentPage === totalPages ? 'not-allowed' : 'pointer'
            }}
          >
            Next
          </button>
        </div>
      </main>
      
      <Footer />
    </div>
  );
};

export default ResultsPage;